import utlis from "../../static/js/utlis.js"
export default {
	data() {
		return {
			phoneNum: '',
			phone: '',
			showModal: 0,
			version: '',
			ver: 0
		}
	},
	onLoad() {
		let phoneNum = uni.getStorageSync('phoneNum')
		this.phoneNum = phoneNum
		this.getUserInfo(this.phoneNum)
	},
	onShow() {
		this.getVersion();
	},
	methods: {
		discount(e) {
			console.log(e.currentTarget.dataset.id)
		},
		about() {
			uni.navigateTo({
				url: '/pages/aboutUs/aboutUs'
			});
		},
		// 客服电话
		customerPhone() {
			this.showModal = 1;
		},
		modal_cancel() {
			this.showModal = 0;
		},
		modal_confirm() {
			this.showModal = 0;
			uni.makePhoneCall({
				// 手机号
				phoneNumber: '4001080448',
				// 成功回调
				success: (res) => {
					console.log('调用成功!')
				},
				// 失败回调
				fail: (res) => {
					console.log('调用失败!')
				}

			});
		},
		version1() {
			uni.navigateTo({
				url: '/pages/version/version'
			});
		},
		exit() {
			uni.clearStorageSync();
			uni.reLaunch({
				url: '/pages/index/index'
			});
		},
		// 获取版本信息
		getVersion() {
			let self = this;
			let data = {
				userSource: 1,
				xmType: 1
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			utlis.http("POST", 'cdxmVersion', data, function(res) {
				if (res.status == "success") {
					self.version = res.version;
					if (self.version != uni.getStorageSync('version')) {
						self.ver = 1;
					} else {
						self.ver = 0;
					}
					uni.hideLoading()
				} else {
					uni.hideLoading()
				}
			})
		},
		//获取用户信息
		getUserInfo(e) {
			let self = this;
			let data = {
				custName: e
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			utlis.http("POST", 'appCustCenter', data, function(res) {
				if (res.status == "success") {
					uni.hideLoading()
					uni.setStorageSync("custName", res.custName);
					self.phone = res.custName;
				} else {
					uni.hideLoading()
				}
			})
		},
		//在线客服
		handleContact(e) {
			console.log(e)
		}
	}
}
